import { Map } from "./map"
import { LazyDiv } from "../lazyDiv"
import { Button } from "../button"
import ImageNhaTrai from "../../image/address/1.jpg"
import ImageNhaGai from "../../image/address/2.jpg"


export const Location = () => {

  const handleOpenMap = ({ lat, lng }: any) => () => {
    // ask allow get current location

    try {

      navigator.permissions
        .query({
          name: "geolocation"
        })
        .then(permission => {
          if (permission.state === "granted") {
            navigator.geolocation.getCurrentPosition(({ coords }) => {
              const { latitude, longitude } = coords
              window.open(
                `https://www.google.com/maps/dir/${latitude},${longitude}/${lat},${lng}`
              )
            })
          }

          if (permission.state === "prompt") {
            navigator.geolocation.getCurrentPosition(({ coords }) => {
              const { latitude, longitude } = coords
              window.open(
                `https://www.google.com/maps/dir/${latitude},${longitude}/${lat},${lng}`
              )
            })
          }

          if (permission.state === "denied") {
            window.open(
              `https://www.google.com/maps/dir/${lat},${lng}`
            )
          }

          permission.onchange = () => {
            if (permission.state === "granted") {
              navigator.geolocation.getCurrentPosition(({ coords }) => {
                const { latitude, longitude } = coords
                window.open(
                  `https://www.google.com/maps/dir/${latitude},${longitude}/${lat},${lng}`
                )
              })
            }

            if (permission.state === "prompt") {
              navigator.geolocation.getCurrentPosition(({ coords }) => {
                const { latitude, longitude } = coords
                window.open(
                  `https://www.google.com/maps/dir/${latitude},${longitude}/${lat},${lng}`
                )
              })
            }

            if (permission.state === "denied") {
              window.open(
                `https://www.google.com/maps/dir/${lat},${lng}`
              )
            }
          }
        })
    } catch (error) {
      console.error(error)
    }

  }



  return (
    <>
      <LazyDiv className="card location">
        <div className="addr">
          <span style={{ fontWeight: 700, textTransform: "uppercase", fontSize: 24 }}>Nh Trai</span>
          <div>
            Khai Tic: 10:30 AM
          </div>
          <div className="detail" style={{ fontSize: "14px "}}>
            Thn nh Thnh, x Ho nh ng,  huyn Ph Ho, Ph Yn
          </div>
        </div>
        <img src={ImageNhaTrai} alt="Nh Trai" />

        <div className="break" />
        <Button type="button" style={{ cursor: "pointer" }} onClick={handleOpenMap({
          lat: 13.035982061058538,
          lng: 109.21992555193266
        })}>
          Ch ng
        </Button>
      </LazyDiv>
      <LazyDiv className="card location">
        <div className="addr">
          <span style={{ fontWeight: 700, textTransform: "uppercase", fontSize: 24 }}>Nh Gi</span>
          <div>
            Khai Tic: 16:00 AM
          </div>
          <div className="detail" style={{ fontSize: "14px " }}>
            Khu ph nh Th 1, TT Ph Ho, huyn Ph Ho, Ph Yn
          </div>
        </div>
        <img src={ImageNhaGai} alt="Nh Gi" />
        <div className="break" />
        <Button type="button" style={{ cursor: "pointer" }} onClick={handleOpenMap({
          lat: 13.020028975132343,
          lng: 109.2133490012639
        })}>
          Ch ng
        </Button>
      </LazyDiv>
    </>
  )
}
